* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
}

body {
    background: linear-gradient(
            90deg,
            rgb(81, 58, 212) 0%,
            rgb(56, 68, 212) 100%
    );
    /*background-color: #4455FF !important;*/
}

.card-bg {
    width: 18rem !important;
    background: linear-gradient(
            90deg,
            rgb(81, 58, 212) 0%,
            rgb(56, 68, 212) 100%
    );
}

.todo-app {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    width: 520px;
    height: 600px;
    background: #161a2b;
    text-align: center;
    margin: 100px auto;
    border-radius: 10px;
    padding-bottom: 32px;
}

.todo-input {
    padding: 14px 32px 14px 16px;
    border-radius: 4px 0 0 4px;
    border: 2px solid #5d0cff;
    outline: none;
    width: 320px;
    background: transparent;
    color: #fff;
}

.todo-input::placeholder {
    color: #e2e2e2;
}

h1 {
    margin-top: 36px !important;
    margin-bottom: 36px !important;
    color: #fff;
    font-size: 24px;
}

.todo-button {
    padding: 16px;
    border: none;
    border-radius: 0 4px 4px 0;
    cursor: pointer;
    outline: none;
    background: linear-gradient(
            90deg,
            rgba(93, 12, 255, 1) 0%,
            rgba(155, 0, 250, 1) 100%
    );
    color: #fff;
    text-transform: capitalize;
}


.todo-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 4px auto;
    color: #fff;
    background: linear-gradient(
            90deg,
            rgba(255, 118, 20, 1) 0%,
            rgba(255, 84, 17, 1) 100%
    );

    padding: 16px;
    border-radius: 5px;
    width: 90%;
}

.todo-form {
    margin-bottom: 32px;
}


.todo-row:nth-child(4n + 1) {
    background: linear-gradient(
            90deg,
            rgba(93, 12, 255, 1) 0%,
            rgba(155, 0, 250, 1) 100%
    );
}

.todo-row:nth-child(4n + 2) {
    background: linear-gradient(
            90deg,
            rgba(255, 12, 241, 1) 0%,
            rgba(250, 0, 135, 1) 100%
    );
}

.todo-row:nth-child(4n + 3) {
    background: linear-gradient(
            90deg,
            rgba(20, 159, 255, 1) 0%,
            rgba(17, 122, 255, 1) 100%
    );
}

.icons {
    display: flex;
    align-items: center;
    font-size: 24px;
    cursor: pointer;
}

.todo-list {
    overflow-y: auto;

}

/* width */
.todo-list::-webkit-scrollbar {
    width: 12px;
    background-color: #F5F5F5;
}


/* Handle */
.todo-list::-webkit-scrollbar-thumb {
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.1);
    background-color: rgba(255, 118, 20, 1);
}

/* Track */
.todo-list::-webkit-scrollbar-track {
    background: #161a2b;
}

